@import "../style/color";

.portfolio {
  padding: 100px 0;

  .title {
    text-align: center;
    margin-bottom: 50px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    overflow: hidden;
    transition: .3s;
    &.active {
      max-height: max-content;
    }
  }

  &__list-item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 720px;
    margin-bottom: 20px;
    border-radius: 10px;
    -webkit-box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.7);
    -moz-box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.7);
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.7);
    overflow: hidden;
    &:before {
      content: '';
      position: absolute;
      transform: scale(0);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: -4;
      transition: .3s;
    }
    &:hover {
      &:before {
        transform: scale(1);
      }

      .item-title {
        transform: scale(1);
      }

      .item-link {
        transform: scale(1);
      }
    }
  }

  .item-img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
    top: 0;
    left: 0;
    z-index: -5;
  }

  .item-title {
    position: relative;
    font-size: 3rem;
    font-weight: 700;
    text-transform: uppercase;
    color: $decor-color;
    margin-bottom: 20px;
    transform: scale(0);
    transition: .3s;
    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      width: 0;
      height: 2px;
      border-radius: 2px;
      background-color: $block-bg;
      transition: .3s;
    }
    &:hover {
      &:before {
        width: 100%;
      }

    }
  }

  &__btn {
    display: block;
    margin: 20px auto;
    padding: 12px 20px;
    font-size: 1.8rem;
    font-weight: 600;
    color: $black-color;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
  }
}